"use client";

import { useState, useCallback, useEffect } from "react";
import { MediaGrid } from "./MediaGrid";
import { UploadArea } from "./UploadArea";
import { Lightbox } from "./Lightbox";
import { Button } from "./ui/button";
import { Upload, Grid, List, RefreshCw } from "lucide-react";

export interface MediaItem {
  id: string;
  type: "image" | "video";
  src: string;
  thumbnail?: string;
  title: string;
  description?: string;
  createdAt: Date | string;
  size?: number;
  originalName?: string;
}

export function Gallery() {
  const [media, setMedia] = useState<MediaItem[]>([]);
  const [selectedItem, setSelectedItem] = useState<MediaItem | null>(null);
  const [viewMode, setViewMode] = useState<"grid" | "list">("grid");
  const [showUpload, setShowUpload] = useState(false);
  const [loading, setLoading] = useState(true);
  const [uploading, setUploading] = useState(false);

  // Fetch media from API
  const fetchMedia = useCallback(async () => {
    try {
      setLoading(true);
      const response = await fetch("/api/media");
      const data = await response.json();
      
      if (data.success) {
        // Convert string dates back to Date objects
        const mediaWithDates = data.media.map((item: MediaItem) => ({
          ...item,
          createdAt: new Date(item.createdAt),
        }));
        setMedia(mediaWithDates);
      }
    } catch (error) {
      console.error("Error fetching media:", error);
    } finally {
      setLoading(false);
    }
  }, []);

  // Load media on component mount
  useEffect(() => {
    fetchMedia();
  }, [fetchMedia]);

  // Handle file upload
  const handleUpload = useCallback(async (files: File[]) => {
    try {
      setUploading(true);
      
      const formData = new FormData();
      files.forEach((file) => formData.append("files", file));
      
      const uploadResponse = await fetch("/api/upload", {
        method: "POST",
        body: formData,
      });
      
      const uploadResult = await uploadResponse.json();
      
      if (uploadResult.success) {
        // Save to media database
        const saveResponse = await fetch("/api/media", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ files: uploadResult.files }),
        });
        
        const saveResult = await saveResponse.json();
        
        if (saveResult.success) {
          // Convert string dates back to Date objects
          const mediaWithDates = saveResult.media.map((item: MediaItem) => ({
            ...item,
            createdAt: new Date(item.createdAt),
          }));
          setMedia(mediaWithDates);
        }
      }
    } catch (error) {
      console.error("Error uploading files:", error);
    } finally {
      setUploading(false);
      setShowUpload(false);
    }
  }, []);

  // Handle delete
  const handleDelete = useCallback(async (id: string) => {
    try {
      const response = await fetch(`/api/media/${id}`, {
        method: "DELETE",
      });
      
      const result = await response.json();
      
      if (result.success) {
        // Convert string dates back to Date objects
        const mediaWithDates = result.media.map((item: MediaItem) => ({
          ...item,
          createdAt: new Date(item.createdAt),
        }));
        setMedia(mediaWithDates);
        
        if (selectedItem?.id === id) {
          setSelectedItem(null);
        }
      }
    } catch (error) {
      console.error("Error deleting media:", error);
    }
  }, [selectedItem]);

  return (
    <div className="space-y-6">
      {/* Controls */}
      <div className="flex flex-col sm:flex-row gap-4 items-center justify-between">
        <div className="flex items-center gap-2">
          <Button
            variant={viewMode === "grid" ? "default" : "outline"}
            size="sm"
            onClick={() => setViewMode("grid")}
            disabled={loading}
          >
            <Grid className="w-4 h-4 mr-2" />
            Grid
          </Button>
          <Button
            variant={viewMode === "list" ? "default" : "outline"}
            size="sm"
            onClick={() => setViewMode("list")}
            disabled={loading}
          >
            <List className="w-4 h-4 mr-2" />
            List
          </Button>
          <Button
            variant="outline"
            size="sm"
            onClick={fetchMedia}
            disabled={loading}
            className="ml-2"
          >
            <RefreshCw className={`w-4 h-4 mr-2 ${loading ? "animate-spin" : ""}`} />
            Refresh
          </Button>
        </div>
        
        <Button
          onClick={() => setShowUpload(!showUpload)}
          disabled={loading || uploading}
          className="bg-gradient-to-r from-violet-600 to-indigo-600 hover:from-violet-700 hover:to-indigo-700 disabled:opacity-50"
        >
          <Upload className={`w-4 h-4 mr-2 ${uploading ? "animate-bounce" : ""}`} />
          {uploading ? "Uploading..." : "Upload Media"}
        </Button>
      </div>

      {/* Upload Area */}
      {showUpload && (
        <div className="animate-in slide-in-from-top-2 duration-300">
          <UploadArea 
            onUpload={handleUpload} 
            onCancel={() => setShowUpload(false)}
            uploading={uploading}
          />
        </div>
      )}

      {/* Media Grid */}
      <MediaGrid
        media={media}
        viewMode={viewMode}
        onItemClick={setSelectedItem}
        onItemDelete={handleDelete}
      />

      {/* Lightbox */}
      {selectedItem && (
        <Lightbox
          item={selectedItem}
          onClose={() => setSelectedItem(null)}
          onNext={() => {
            const currentIndex = media.findIndex((item) => item.id === selectedItem.id);
            const nextIndex = (currentIndex + 1) % media.length;
            setSelectedItem(media[nextIndex]);
          }}
          onPrev={() => {
            const currentIndex = media.findIndex((item) => item.id === selectedItem.id);
            const prevIndex = currentIndex === 0 ? media.length - 1 : currentIndex - 1;
            setSelectedItem(media[prevIndex]);
          }}
        />
      )}

      {/* Stats */}
      <div className="text-center text-sm text-slate-500 dark:text-slate-400 bg-white/50 dark:bg-slate-800/50 rounded-full px-6 py-2 backdrop-blur-sm border border-slate-200 dark:border-slate-700 inline-block">
        {media.length} items in gallery • {media.filter(m => m.type === "image").length} images • {media.filter(m => m.type === "video").length} videos
      </div>
    </div>
  );
}